import React from "react"
import PropTypes from "prop-types"
import Link from "gatsby-link"
import Helmet from "react-helmet"
import { rhythm } from "../utils/typography"


const FooterLinks = [
  {
    name: 'Twitter',
    link: 'https://twitter.com/stolinski'
  }, {
    name: 'Youtube',
    link: 'http://www.youtube.com/user/LevelUpTuts'
  }, {
    name: 'Github',
    link: 'https://github.com/stolinski'
  }, {
    name: 'BitBucket',
    link: 'https://bitbucket.org/stolinski'
  }, {
    name: 'Facebook',
    link: 'https://www.facebook.com/LevelUpTuts'
  }
]


export default class Template extends React.Component {
  static propTypes = {
    children: PropTypes.func,
  }

  render() {
    return (
      <div>
        <Helmet
          title="Scott Tolinski"
          meta={[
            { name: "description", content: "Sample" },
            { name: "keywords", content: "sample, something" },
          ]}
        />

        <header className="header">
          <div className="header-wrap">
            <Link className="homelink" to="/"><span className="logo">ST</span>
              <h1 className="name">Scott Tolinski</h1>
            </Link>
            <nav className="main-nav">
              <ul>
                <li><Link className="about" to="/about">About</Link></li>
                <li><Link className="projects" to="/projects">Projects</Link></li>
                <li><Link className="contact" to="/contact">Contact</Link></li>
              </ul>
            </nav>
          </div>
        </header>

        <div
          style={{
            margin: `0 auto`,
            maxWidth: 960,
            padding: `${rhythm(1)} ${rhythm(3 / 4)}`,
            paddingTop: 0,
          }}
        >
          {this.props.children()}
        </div>

      <footer className="footer">
        <h3>Where to find me</h3>
        <ul className="social">
          {FooterLinks.map(link => (
            <li key={link.name}>
              <a target="_blank" rel="nofollow" className={link.name.toLowerCase()} href={link.link}>
                <span className="text">{link.name}</span>
              </a>
            </li>
          ))}
        </ul>
      </footer>
      </div>
    )
  }
}



